import styled from 'styled-components';
import ContentContainer from '../../components/ContentContainer';
import { getIncome } from '../../utils/helper';
import { stockList } from '../../assets/data/stockList';

type DataType = 'date' | 'code' | 'name' | 'preClose' | 'open' | 'hight' | 'low' | 'close' | 'income';
const headerList: DataType[] = ['date', 'code', 'name', 'preClose', 'open', 'hight', 'low', 'close', 'income'];

const Table = styled.div`
    .header {
        display: flex;
        & > div {
            flex: 1;
            border: 1px solid grey;
            text-align: center;
            padding: 1rem 0 1rem 0;
        }
    }

    .data {
        display: flex;
        & > div {
            flex: 1;
            border: 1px solid grey;
            text-align: center;
            padding: 1rem 0 1rem 0;
        }
    }
`;

export default function Stock() {
    const list = stockList.map(el => {
        const { open: base, hight, low, open, close } = el;
        const income = getIncome({ base: Number(base), hight: Number(hight), limit: 2, low: Number(low), open: Number(open), close: Number(close) });

        return { ...el, income };
    });

    return (
        <ContentContainer>
            <Table>
                <div className='header'>
                    {headerList.map(el => (
                        <div key={el}>{el}</div>
                    ))}
                </div>
                {list.map((el, i) => (
                    <div key={`data-${i}`} className='data'>
                        {headerList.map(el2 => (
                            <div key={el2}>{el[el2] && el[el2]}</div>
                        ))}
                    </div>
                ))}
            </Table>
        </ContentContainer>
    );
}
